@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap");

* {
  font-family: "Montserrat", "SUITE-Regular";
}

.loginList {
  display: flex;
  min-height: 100vh;
  .loginList_imgBox {
    width: 60%;
    min-width: 860px;
    background-color: azure;
  }

  .loginList_LoginBox {
    width: 40%;
    min-width: 560px;
    padding-top: 15%;
    .loginList_LoginBox_div {
      width: 30%;
      margin: auto;
      min-width: 480px;
      ul {
        width: 70%;
        min-width: 400px;
        margin: auto;
        h2 {
          margin: 7% 0;
        }
        form {
          li {
            padding: 5px 0;
            input {
              display: block;
              border: none;
              background-color: whitesmoke;
              border-radius: 5px;
              width: 284px;
              height: 48px;
              font-size: 0.8rem;
              padding: 0 10px;
              &[type="submit"] {
                width: 304px;
                margin-top: 20px;
                background-color: black;
                color: white;
                font-size: 1.1rem;
                cursor: pointer;
              }
            }
          }
        }
        p {
          margin-top: 15%;
          font-size: 1rem;
          text-align: center;
          a {
            font-size: 1rem;
            text-decoration: underline;
            font-weight: 550;
          }
        }
      }
    }
  }
}

.failBox {
  margin-top: 5px;
  border-radius: 5px;
  background-color: #cc5b5b;
  color: white;
  padding-top: 4%;
  font-size: 0.9rem;
  width: 304px;
  height: 25px;
  text-align: center;
  line-height: 10px;
}
